<template>
  <el-input
    class="l-spinner"
    :size="size"
    v-model="sonValue.stringValue"
    :disabled="disabled"
    :placeholder="placeholder"
    :clearable="clearable"
    :readonly="readonly"

    @keyup.enter.native="callEvent"
    @blur="callEvent">
    <template slot="prepend">
      <slot name="prepend"></slot>
    </template>
    <template slot="append">
      <slot name="append"></slot>
    </template>
  </el-input>
</template>

<script>
export default {
  name: "l-spinner",
  props: {
    value: {},
    placeholder: String,
    size: {
      type:String,
      default:'mini'
    },
    disabled: Boolean,
    readonly: Boolean,
    clearable: {
      type: Boolean,
      default: true,
    },
    retainNumber: {       //
      type: Number,
      default: 2,
    },
    isChinese:{           //
      type:Boolean,
      default:false,
    }
  },
  mounted(){
  },
  data() {
    return {
      sonValue: {
        numberValue: undefined,
        stringValue: "",
      },
    };
  },
  watch: {
    value() {
      if(this.value){
       this.sonValue.numberValue = Number(this.value);
       if(!this.isChinese){
          this.sonValue.stringValue = Number(this.value).toFixed(this.retainNumber).toString().replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
       }else{
          this.sonValue.stringValue = this.convertToCharacterNum(this.value)
       }
      }else if(this.value==0){
       this.sonValue.numberValue = 0;
       this.sonValue.stringValue = "0";
       this.$emit("input", this.sonValue.numberValue);
      }else{
       this.sonValue.numberValue = undefined;
       this.sonValue.stringValue = "";
       this.$emit("input", this.sonValue.numberValue);
      }
    },
  },

  methods: {
    handleBlur() {                             //clearableundefined
      if (this.sonValue.stringValue == "") {
        this.sonValue.numberValue = undefined;
      } else {
        const value = this.sonValue.stringValue.replace(/,/g, "");
        let reg = /^[\.\d]*$/;
        if (reg.test(value)) {
          this.sonValue.numberValue = Number(value).toFixed(this.retainNumber);
          this.sonValue.stringValue = Number(value).toFixed(this.retainNumber).toString().replace(/(\d)(?=(\d{3})+\.)/g, "$1,");
        } else {
          this.sonValue.stringValue = "";
          this.sonValue.numberValue = undefined;
        }
      }
      this.$emit("input", this.sonValue.numberValue);
    },

    handleEnter(){
      if (this.sonValue.stringValue == "") {
        this.sonValue.numberValue = undefined;
      } else {
        const value = this.sonValue.stringValue.replace(/,/g, "");
        let reg = /^[\.\d]*$/;
        if (reg.test(value)) {
          this.sonValue.numberValue = Number(value);
          this.sonValue.stringValue = this.convertToCharacterNum(this.value)
        } else {
          this.sonValue.stringValue = "";
          this.sonValue.numberValue = undefined;
        }
      }
      this.$emit("input", this.sonValue.numberValue);
    },

    convertToCharacterNum(num){
      let arr1 = ['', '', '', '', '', '', '', '', '', ''];
      let arr2 = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',''];
      if (!num || isNaN(num)) {
          return "";
      }

      let integer=Math.trunc(num)                  //1.
      let decimal=num.toString().split(".")[1]

      let english = integer.toString().split("");   //2.
      let integerResult = "";
      for (let i = 0; i < english.length; i++) {
          let des_i = english.length - 1 - i;
          integerResult= arr2[i] + integerResult;
          let arr1_index = english[des_i];
          integerResult = arr1[arr1_index] + integerResult;
      }
      integerResult = integerResult.replace(/(||)/g, '').replace(//g, '');
      integerResult = integerResult.replace(/+/g, '');
      integerResult = integerResult.replace(//g, '').replace(//g, '');
      integerResult = integerResult.replace(//g, '');
      integerResult = integerResult.replace(/+$/, '')

      let decimalResult=""                          //3.()
      if(decimal){
        let decimalArray=decimal.split("")
        decimalArray.forEach(e => {              // "11.1111"   ""
            decimalResult+=arr1[e]
        });
        // decimalResult=arr1[decimalArray[0]]+""+arr1[decimalArray[1]]+""
      }

      let dataResult=''                             //4.
      if(integerResult&&decimalResult){
        dataResult=integerResult+""+decimalResult
      }else if(integerResult==""&&decimalResult){
         dataResult=""+""+decimalResult
      }else if(integerResult&&decimalResult==""){
        dataResult=integerResult
      }
      return dataResult;
    },

    callEvent(){
     this.isChinese?this.handleBlur():this.handleEnter()        //
    },

  }
}
</script>
<style lang="less">
@import "./index.less";
</style>
