<template>
  <img :key="id" v-if="qrUrl || format != 'qr'" ref="barcode" :src="qrUrl" />
</template>
<script>
import JsBarcode from "jsbarcode";
export default {
  name: "l-barcode",
  props: {
    value: String,
    format: {
      type: String,
      default: "CODE128" //CODE128CODE128ACODE128BCODE128C EAN13EAN8EAN5EAN2UPCCODE39ITFITF14
      //MSI10MSI11MSI1010MSI1110pharmacodecodabarqr
    },
    width: {
      // 
      type: Number,
      default: 2
    },
    height: {
      // 
      type: Number,
      default: 100
    },
    displayValue: {
      type: Boolean, // 
      default: true
    },
    text: String,
    fontOptions: {
      // 
      type: String,
      default: ""
    },
    font: {
      // 
      type: String,
      default: "monospace"
    },
    textAlign: {
      // 
      type: String,
      default: "center"
    },
    textPosition: {
      // 
      type: String,
      default: "bottom"
    },
    textMargin: {
      // 
      type: Number,
      default: 2
    },
    fontSize: {
      // 
      type: Number,
      default: 20
    },
    background:String,
    color: {
      // 
      type: String,
      default: "#000000"
    },
    margin:  {
      type: Number,
      default: 0
    },
    marginTop: Number,
    marginRight: Number,
    marginBottom: Number,
    marginLeft: Number,
    flat: {
      // EAN8/EAN13
      type: Boolean,
      default: false
    },

    // 
    size: {
      type: Number,
      default: 400
    },
    correctLevel: {
      //
      type: Number,
      default: 0
    },
    autoColor: {
      // color
      type: Boolean,
      default: true
    },

    // 
    backgroundImage: String,
    backgroundDimming: String, // 
    gifBackground: String,

    whiteMargin: {
      // 
      type: Boolean,
      default: true
    },

    logoImage:String,
    logoScale: {
      type: Number,
      default: 0.2
    },
    logoMargin: {
      type: Number,
      default: 6
    },
    logoCornerRadius: {
      type: Number,
      default: 8
    },
  },
  computed: {
    options() {
      const options = {};

      if (this.format == "qr") {
        options.text = this.value;
        options.size = this.size;
        options.margin = this.margin;
        options.correctLevel = this.correctLevel;
        options.colorDark = this.color;
        options.colorLight = this.background;
        options.autoColor = this.autoColor;

        options.backgroundImage = this.backgroundImage;
        options.backgroundDimming = this.backgroundDimming;
        options.gifBackground = this.gifBackground;

        options.whiteMargin = this.whiteMargin;

        options.logoImage = this.logoImage;
        options.logoScale = this.logoScale;
        options.logoMargin = this.logoMargin;
        options.logoCornerRadius = this.logoCornerRadius;

      } else {
        options.format = this.format;
        options.width = this.width;
        options.height = this.height;
        options.text = this.text;
        options.fontOptions = this.fontOptions;
        options.textAlign = this.textAlign;
        options.textPosition = this.textPosition;
        options.textMargin = this.textMargin;
        options.fontSize = this.fontSize;
        options.background = this.background;
        options.lineColor = this.color;

        options.margin = this.margin;
        options.marginTop = this.marginTop;
        options.marginRight = this.marginRight;
        options.marginBottom = this.marginBottom;
        options.marginLeft = this.marginLeft;

        options.displayValue = this.displayValue;

        options.flat = this.flat;
      }

      //console.log(options,'options');

      return options;
    }
  },
  watch: {
    value: {
      handler() {
        this.showCode();
      },
      immediate: true
    },
    options: {
      handler() {
        this.showCode();
      }
    }
  },
  data() {
    return {
      id:1,
      qrUrl: ""
    };
  },
  methods: {
    showCode() {
      if (this.format == "qr") {
        if (!window.AwesomeQR) {
          console.error("awesome-qr.js");
          return;
        }
        new window.AwesomeQR.AwesomeQR(this.options)
          .draw()
          .then(dataURL => {
            this.qrUrl = dataURL;
            this.id++;
          })
          .catch(err => {
            console.error(err);
          });
      } else {
        if (this.$refs.barcode) {
          JsBarcode(this.$refs.barcode, this.value, this.options);
        } else {
          setTimeout(() => {
            this.showCode();
          }, 100);
        }
      }
    }
  }
};
</script>
